<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col xs="12" lg="12" class="d-block">
        <b-card>
          <b-row>
           <b-col xs="12" lg="12" class="dashboard-content">
        <b-card>
          <b-form-select
            id="tab-selector"
            v-model="tab_selector"
            @change="tabSelect"
            required
            class="d-block d-lg-none"
          >
            <option value="" selected disabled>Choose</option>
            <option v-for="tab in tabs" :value="tab" :key="tab.index"><i class="tab['icon']"></i>{{tab['title']}}</option>
          </b-form-select>
          <b-tabs v-model="tabIndex">
            <b-tab active>
              <template slot="title">
                <i class="icon-bell"></i> {{tabs[0]['title']}}
              </template>
              <b-card class="d-none d-lg-block new-caregiver-btn">
              </b-card>
              <NotificationTable :columns="columns" :data="all_notifications" :options="options" :theme="theme" :userId="userId" ></NotificationTable>
            </b-tab>
            <b-tab>
              <template slot="title">
                <i class="icon-plus"></i> {{tabs[1]['title']}}
              </template>
              <b-card class="d-none d-lg-block new-caregiver-btn">
              </b-card>
              <NotificationTable :columns="columns" :data="newCaregiverNotifications" :options="options" :theme="theme" :userId="userId" ></NotificationTable>
            </b-tab>

            <b-tab>
              <template slot="title">
                <i class="icon-close"></i> {{tabs[2]['title']}}
              </template>
              <b-card class="d-none d-lg-block new-caregiver-btn">
              </b-card>
                <NotificationTable :columns="columns" :data="deleteCaregiverNotifications" :options="options" :theme="theme" :userId="userId" ></NotificationTable>
            </b-tab>    
          </b-tabs>
        </b-card>
      </b-col>     
          </b-row>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios';
import NotificationTable from './NotificationTable.vue'
import {restapiPrefix} from './variables.js'
import { ClientTable } from "vue-tables-2";
import { random } from '@/shared/utils'
import router from '../router';


let options = {};
let useVuex = false;
let theme = "bootstrap4";
let template = "default";
Vue.use(ClientTable, options, useVuex, theme, template);

export default {
  name: 'notifications',
  components: {
    NotificationTable,
    //CaregiverRegister
  },
  data: function ()  {
    return {
      tab_selector: '',
      tabIndex: 0,
      activeTab:0,
      tabs: [
         {
          title: 'All',
          icon: 'fa fa-list'
        },
        {
          title: 'NEW CAREGIVERS',
          icon: 'fa fa-plus'
        },
        {
          title: 'DELETED CAREGIVERS',
          icon: 'fa fa-list'
        }
      ],
      columns: ['id', 'description'],
      all_notifications:[],
      newCaregiverNotifications:[],
      deleteCaregiverNotifications:[],
      options: {
        headings: {
          // checkbox: '<input type="checkbox" class="custom-control-input" id="customInChk1" value="1">',
          // checkbox: {
          //   render (c) { return c('<input type="checkbox" class="form-control" id="customInChk1" value="1">') }
          // },

        },
        columnsClasses: {
          initial: 'initial-class',
          progress: 'progress-class',
        },
        rowClassCallback(row) {
          return row.flag?"noti_unread":"noti_read";
        },
        sortable: ['id', 'description'],
        sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
        pagination: {
          chunk: 5,
          edge: false,
          nav: 'scroll'
        }
      },
      //useVuex: false,
      theme: 'bootstrap4',
    }
  },
  beforeCreate: function () {  
    if(localStorage.user_id == undefined || localStorage.user_id == "0")
    {        
      router.push({ name: "Login"})
    }   
  },
  created() {
    this.userId = localStorage.user_id;
      if(this.$route.query.type !== undefined )
    {
      if(String(this.$route.query.type) == 'newCaregivers'){
        this.activeTab = 1;
      }
      else if(String(this.$route.query.type) == 'delCaregivers'){
         this.activeTab = 2;
      }
      else{
         this.activeTab = 0;
      }
    }
    this.$nextTick(() => {
      this.tabIndex = this.activeTab
    })
    this.getAllNotifications(this.userId);
  },
  methods: {
    tabSelect(e) {
      this.tabIndex = this.tabs.indexOf(e)
    },
    getAllNotifications(userId) {
      let asksUri = restapiPrefix+'/users/getAllNotifications/'+userId;
      this.axios.get(asksUri).then(response => {
        if(response.data.length > 0){
          this.all_notifications = response.data[0];
          this.newCaregiverNotifications = response.data[1];
          this.deleteCaregiverNotifications = response.data[2];
        }
      })
    }
  }
}
</script>
<style lang="scss">
.nav-tabs {
  font-size: 15px;
}
.nav-tabs .nav-link.active, .nav-tabs .nav-item.show .nav-link {
    border: none !important;
}
.nav-tabs .nav-link:hover, .nav-tabs .nav-link:focus {
    border: none !important;
}
.nav-tabs > li > a.active > i {
    color: #f86c6b;
}
</style>