<template>
  <div>
    <v-client-table :columns="columns" :data="data" :options="options" :theme="theme" id="dataTable"  @row-click='rowClicked'>

    </v-client-table>
  </div>
</template>
<script>
import Vue from 'vue'
import {ClientTable, Event} from 'vue-tables-2'
import TrainingProgress from './TrainingProgress'
import {restapiPrefix} from './variables.js'
import router from '../router';

Vue.use(ClientTable)

export default {
  name: 'DashboardTable',
  props: ["columns", "data", "options","theme", "userId"],
  components: {
    ClientTable,
    Event,
  },
  data () {
    return {
         }
  },
  created() {
    this.userId = localStorage.user_id;
  },
  methods : {
    rowClicked: function ({row}) {
      //alert(row.flag);
      if(row.flag == 1)
      {
        let asksUri = restapiPrefix+'/users/updateNotifications/'+this.userId+'/'+row.id;
        this.axios.get(asksUri).then(response => {
          if(response.data.length > 0){
            console.log(response.data);
            //this.all_notifications = response.data[0];
            //this.newCaregiverNotifications = response.data[1];
            //this.deleteCaregiverNotifications = response.data[2];
          }
        })
      }
      router.push({ name: "Dashboard"})
    },
    getClass : function (flag,classFor) {
      if(classFor == 'noti_read')
      {
        return flag == 0;
      }
      if(classFor == 'noti_unread')
      {
        return flag == 1;
      }
    },
  }
}
</script>

